<template>
     <div class="carousel">
          <agile class="main" ref="main" :options="options1" :as-nav-for="asNavFor1">
               <div class="slide" v-for="(slide, index) in slides" :key="index" :class="`slide--${index}`"><img :src="slide"/></div>
          </agile>
          <agile class="thumbnails" ref="thumbnails" :options="options2" :as-nav-for="asNavFor2">
               <div class="slide slide--thumbniail" v-for="(slide, index) in slides" :key="index" :class="`slide--${index}`" @click="$refs.thumbnails.goTo(index)"><img :src="slide"/></div>
               <template slot="prevButton"><i class="fas fa-chevron-left"></i></template>
               <template slot="nextButton"><i class="fas fa-chevron-right"></i></template>
          </agile>
     </div>
</template>

<script>
import { VueAgile } from 'vue-agile';

export default {
     name: 'Carousel',
     data(){
          return {
               asNavFor1: [],
			asNavFor2: [],
			options1: {
				dots: false,
				fade: true,
				navButtons: false
			},
			options2: {
				autoplay: true,
				centerMode: true,
				dots: false,
				navButtons: false,
				slidesToShow: 3,
				responsive: [
                         {
                              breakpoint: 600,
                              settings: {
                                   slidesToShow: 5
                              }
                         },
                         
                         {
                              breakpoint: 1000,
                              settings: {
                                   navButtons: true
                              }
                         }
                    ]
			},
			slides: [
				'https://images.unsplash.com/photo-1504639725590-34d0984388bd?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=967&q=80',
                    'https://images.unsplash.com/photo-1499209974431-9dddcece7f88?ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80',
                    'https://images.unsplash.com/photo-1444703686981-a3abbc4d4fe3?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80',
                    'https://images.unsplash.com/photo-1508690471604-cafd081190a1?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=986&q=80',
                    'https://images.unsplash.com/photo-1519791883288-dc8bd696e667?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80'
               ]
		}
     },
     components:{
          agile: VueAgile,
     },
     mounted () {
		this.asNavFor1.push(this.$refs.thumbnails)
		this.asNavFor2.push(this.$refs.main)
	}
};
</script>

<style scoped lang="sass">
#app
     font-family: 'Lato', sans-serif
     font-weight: 300
     margin: 0 auto
     max-width: 900px
     padding: 30px
.main
     margin-bottom: 10px
.thumbnails
     margin-right:10px
     margin-left:10px
.agile
     &__nav-button
          background: transparent
          border: none
          color: #ccc
          cursor: pointer
          font-size: 24px
          transition-duration: .3s
          .thumbnails &
               position: absolute
               top: 50%
               transform: translateY(-50%)
               &--prev
                    left: -45px
               &--next
                    right: -45px
          &:hover
               color: #888
     &__dot
          margin: 0 10px
          button 
               background-color: #eee
               border: none
               border-radius: 50%
               cursor: pointer
               display: block
               height: 10px
               font-size: 0
               line-height: 0
               margin: 0
               padding: 0
               transition-duration: .3s
               width: 10px
          &--current,
          &:hover
               button
                    background-color: #888
.slide
     align-items: center
     box-sizing: border-box
     color: #fff
     display: flex
     height: 610px
     justify-content: center
     &--thumbniail
          cursor: pointer
          height: 100px
          padding: 0 5px
          transition: opacity .3s
          &:hover
               opacity: .75
     img
          height: 100%
          object-fit: cover
          object-position: center
          width: 100%
</style>