<template>
     <v-timeline dense>
          <v-timeline-item 
               v-for="(position,i) in positions"
               :key="i"
               :large="true"
               class="timeline_item"
          >
               <v-card >
                    <v-card-title class="title row">
                         <span class="position_title text-uppercase word_break col-sm-6">{{position.title}}</span>
                         <span class="position_company col-sm-6" style="text-align:right">
                              <a 
                                   v-if="position.website" 
                                   :href="position.website"
                                   style="color:rgb(57, 71, 204);"
                                   class="word_break"
                              >
                                   {{position.company}}
                              </a>
                              <span class="word_break" v-else>{{position.company}}</span>
                              <p class="position_date word_break">
                                   {{position.dateFrom}} - {{position.dateTo}} ({{getDuration(position.dateFrom, position.dateTo)}})
                              </p>
                         </span>
                    </v-card-title>
                    <v-card-text class="white text--primary text-left">
                         <ul class="position_description">
                              <li v-for="(desc,j) in position.description" :key="j">
                                   {{position.description[j]}}
                              </li>
                         </ul>
                    </v-card-text>
               </v-card>
          </v-timeline-item>
     </v-timeline>
</template>

<script>
export default {
     name: 'Experience',
     data(){
          return{
               positions:[
                    {
                         title:'Software Developer',
                         company:'Tecfrac',
                         website:'tecfrac.com',
                         dateFrom:'Nov 2019',
                         dateTo:'Present',
                         description:[
                              'The work focus on developing cross-platform and responsive desktop software using Electron.js/Vue.js along with secure and fast back-end services using Spring Boot/Hibernate.',
                              'Tasks vary through all faces of development from requirements and design to deployment and maintenance'
                         ]
                    },
                    {
                         title:'Undergraduate Research Assistant',
                         company:'Lebanese American University',
                         website:'lau.edu',
                         dateFrom:'2018',
                         dateTo:'2019',
                         description:[
                              'Researched optimising a hybrid heuristic that is based on a steady-state genetic algorithm for the NP-hard Maximum Clique problem',
                              'The goal was achieved using shared-memory and GPUs, after studying the accelerated regions via profilers using DIMACS benchmarks on a Xeon processor and NVIDIA GTX 980 GPU.'
                         ]
                    },
                    {
                         title:'Part-time Software Developer',
                         company:'Safa Group for Marble & Granite',
                         website:'http://safa-group.com/',
                         dateFrom:'Jan 2019',
                         dateTo:'April 2019',
                         description:[
                              'Developed a full-stack web application for their machine maintenance and tasks scheduling services using the LAMP stack and Google Calendar API',
                         ]
                    },
                    {
                         title:'Freelance Software Developer',
                         company:'Self-employed',
                         website:'',
                         dateFrom:'2017',
                         dateTo:'2019',
                         description:[
                              'Delivered full-stack web applications for startups and clients',
                              'Supported a team of data analysts in data manipulation and developing analytics algorithms for a healthcare-related project using PostgreSQL',
                              'Collaborated with software that runs facility planning algorithms Java/JavaFX'
                         ]
                    },
                    {
                         title:'Software Engineering Intern',
                         company:'Techpunto',
                         website:'http://techpunto.com/',
                         dateFrom:'Jun 2018',
                         dateTo:'Jul 2018',
                         description:[
                              'Designed and implemented a scalable election management system by optimising a previous version using the LAMP stack',
                              'Tasks: database design, back-end security, front-end design, and client training'
                         ]
                    },
                    {
                         title:'Sales Assistant',
                         company:'BE Home Appliances',
                         website:'',
                         dateFrom:'2013',
                         dateTo:'Sep 2016',
                         description:[
                              'Increased sales by 17%',
                         ]
                    }
               ]
          }
     },
     methods:{
          getDuration(from, to){
               from = new Date(from);
               to = (to == "Present") ? new Date() : new Date(to);
               var diff = to - from; 
               diff /= 1000;
               diff /= 60;
               diff /= 60;
               diff /= 24;
               diff /= 30;
               diff = Math.round(diff)
               var years = 0, months = 0;
               while(diff >= 12){
                    years++;
                    diff -= 12;
               }
               months = diff;
               var yearsText = "", monthsText = "";
               if(years > 0){
                    if(years > 1)
                         yearsText = years + " yrs";
                    else
                         yearsText = years + " yr";
               }
               if(months > 0){
                    if(months > 1)
                         monthsText = months + " mos";
                    else
                         monthsText = months + " mo";
               }
               var res = "";
               if(yearsText && monthsText)
                    res = yearsText + " " + monthsText;
               else
                    res = yearsText + monthsText;
               return res;
          }
     }
};
</script>

<style scoped>
.position_description{
     padding-left:15px;
}
.position_date{
     font-size: 17px;
     font-style: italic;
}
.position_title{
     font-family: 'Copperplate';
     font-weight: bold;
}
.position_company{
     font-family: 'Rockwell';
     color:rgb(57, 71, 204);
}
.timeline_item{
     margin-right:20px
}
.word_break{
     width:100%;
     word-break: normal;
}
</style>